<template>
  <a @click="savePost">savePost</a>
</template>

<script>
  import Api from 'src/api'

  export default {
    methods: {
      savePost () {
        Api.Post.savePost({
          name: '你好',
          content: '12312122'
        })
      }
    }
  }
</script>




